// Slider.js
import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import im2 from '../src/images/im2.png';
import im3 from '../src/images/im3.png';
import im4 from '../src/images/im5.jpg';
import im5 from '../src/images/im5.png';
import im6 from '../src/images/im8.png';
import im7 from '../src/images/im9.png';

const CustomSlider = () => {

    const sliderSettings = {
        dots: true,
        infinite: true,
        speed: 5000,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
      };

  return (
    <Slider {...sliderSettings} className="slider">
    <div className="slider-item">
      <img src={im7} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons" >
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    <div className="slider-item">
      <img src={im5} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons" >
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    <div className="slider-item">
      <img src={im6} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons">
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    <div className="slider-item">
      <img src={im2} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons">
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    <div className="slider-item">
      <img src={im3} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons">
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    <div className="slider-item">
      <img src={im4} alt="Image 5" style={{ width: '100%', height: '100vh', objectFit: 'cover' }} />
      <div className="slider-content">2024</div>
      <div className="slider-buttons">
        <button className="slider-button">Shop Now</button>
        <button className="slider-button">Category</button>
      </div>
    </div>
    
  </Slider>
  );
};

export default CustomSlider;
