// Category.js

import React from 'react';
import styled, { keyframes } from 'styled-components';
import CategoryBox from './CategoryBox';
import data from './data.json';
import cat1 from '../src/images/cat1.jpg';
import cat2 from '../src/images/cat2.jpg';
import cat3 from '../src/images/cat3.jpg';
import cat4 from '../src/images/cat4.jpg';
import cat5 from '../src/images/cat5.jpg';
import f1 from '../src/images/f1.jpg';
import f2 from '../src/images/f2.jpg';


const CategoryComponent = () => {
    const CategoryContainer = styled.div`
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: 20px;
    margin-top: 40px;
    width: 100%;
    margin-left:23px;
    justify-content: space-between;
  `;
  const textStyle={
    marginTop:'100px',
    marginLeft:'35px',
    fontWeight:'bold',
    fontFamily: 'Droid Sans' ,
  }
  
  const buttonStyle={
    marginLeft:'800px',
     marginTop:'-57px',
    
  }
  const imageStyle = {
    width: '630px', // Adjust as needed
    height: '400px', // Adjust as needed
    marginLeft:'35px',
    
  };
  const containerStyle = {
    display: 'flex',
    justifyContent: 'space-between', // Adjust as needed
    marginTop:'650px',
  };
  const ButtonAndText={
    marginTop:'-40px',
  }
  const SingleButton={
    marginLeft:'10px'
  }
  const textStyle2 = {
    position: 'absolute',
    top: '50%', // Adjust as needed
    left: '50%', // Adjust as needed
    transform: 'translate(-100%, -90%)', // Center the text
    textAlign: 'center',
    color: '#fff', // Text color
    marginTop:'-1100px',
    marginRight:'50px',
   
  };
  const newYear={
    backgroundColor:'#FF8C00',
    fontWeight:'bold'
  }
  return (
    <div className="category-container">
      <div style={textStyle2}>
          <p style={newYear}>TRENDY ITEM</p>
          <p  style={newYear}> New Year Trend Coming 2024</p>
          <button className='btn btn-outline-light'  style={{backgroundColor: '#FFA500' }} >DISCOVER</button>
        </div>
      <div style={containerStyle}>
        <img src={f1} style={imageStyle}></img>
        <img src={f2} style={imageStyle}></img>
      </div>
      <div style={ButtonAndText}>
      <div style={textStyle}>
        <div >
        <h1 >Brand New Products</h1>
        <small>Browse the huge variety of our products</small>
          </div> 
          <div style={buttonStyle}>
            <button className='btn btn-outline-warning' style={SingleButton}>table lamp</button>
            <button className='btn btn-outline-warning' style={SingleButton}>FURNITURE</button>
            <button className='btn btn-outline-warning'style={SingleButton}>CHAIR</button>
            <button className='btn btn-outline-warning' style={SingleButton}>CLOCK</button>
          </div>
      </div>
      </div>
         <CategoryContainer>
       
      {data.products.map((product, index) => {
        let imagePath = '';

        // Map categories to corresponding images
        switch (product.category) {
          case 'Womens Footwear':
            imagePath = cat1;
            break;
          case 'Mens Footwear':
            imagePath = cat2;
            break;
          case 'Mens Casualwear':
            imagePath = cat3;
            break;
          case 'Mens Formalwear':
            imagePath = cat4;
            break;
            case 'Womens Casualwear':
              imagePath = cat5;
              break;
              case 'Womens Formalwear':
              imagePath = cat5;
              break;
          // Add more cases for other categories
          default:
            imagePath = 'cat5'; // Set a default image or handle as needed
            break;
        }

        return (
          <CategoryBox
            key={index}
            name={product.name}
            price={product.price}
            image={imagePath}
          />
        );
      })}
      </CategoryContainer>
    </div>
  );
};

export default CategoryComponent;
